.am-wrapper, .am-content{
    height: 100%;

    .editor-panels {
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
        -webkit-flex-direction: row;
        -ms-flex-direction: row;
        flex-direction: row;

        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;

        height: 100%;
        padding: 0;

        .editor-panel {
            display: -webkit-box;
            display: -webkit-flex;
            display: -ms-flexbox;
            display: flex;
            height: 100%;
            width: 50%;
            order: -1;

            #ace-text-editor {
                height: 100%;
                width: 100%;
            }
        }
    }
}